import React, { Component } from 'react'
import { Text, View, TouchableOpacity, TextInput,Button } from 'react-native'
import { httpGet } from './http/httpHandler'
import styles from './styles'

export class Demo extends Component {

    constructor(props) {
        super(props)

        this.state = {
            data: [{ name: '123' }],
            fatched: false
        }
    }

    componentDidMount() {
        this.getDataFromApi()
    }


    getDataFromApi() {
        httpGet('connectionService/getConnections').then(
            (result) => {
                if (result) {
                    return this.setState({
                        data: result,
                        fatched: true
                    });
                }

            },
        )
    }

onLogin(){
    alert('login')
}

    render() {
        const { input, container, headerText, loginButton } = styles
        if (this.state.fatched) {
            return (
                <View style={container}>
                <Button title="click me"/>
                 
                </View>
            )
        } else {
            return (
                <View>
                    <Text> Demo from Shared folder  </Text>
                    <Text>Received Data: NO DATA(network error)</Text>
                </View>
            )
        }

    }
}

export default Demo
